const lang = document.querySelector("html").getAttribute("lang");
const language = document.querySelector(".language__item_active");
const list = document.querySelector(".language__items");

language.addEventListener("click", () => {
  list.classList.toggle("language__items_active");
});

const scrollTopGlobalButton = document.querySelector(
  "#global-scroll-top-button"
);

window.addEventListener("scroll", (e) => {
  const TOP = 195;
  const SCROLLED =
    window.pageYOffset ||
    document.documentElement.scrollTop ||
    document.body.scrollTop ||
    0;
  if (SCROLLED > TOP) {
    scrollTopGlobalButton.style.display = "flex";
  } else {
    scrollTopGlobalButton.style.display = "none";
  }
});

scrollTopGlobalButton.addEventListener("click", () => {
  window.scrollTo({
    top: 0,
    behavior: "smooth",
  });
});

const accordions = document.querySelectorAll(".faq-item");

document.addEventListener("DOMContentLoaded", () => {
  accordions.forEach((accordion) => {
    setTimeout(() => {
      const expandedHeight = accordion.offsetHeight;
      const margin = parseFloat(
        window
          .getComputedStyle(accordion.querySelector(".faq-top"))
          .getPropertyValue("margin-bottom")
      );

      const closedHeight =
        accordion.querySelector(".faq-top").offsetHeight + margin + margin;

      accordion.setAttribute("data-expanded-height", expandedHeight);
      accordion.setAttribute("data-height", closedHeight);

      accordion.style.height = `${closedHeight}px`;

      accordion.addEventListener("click", (e) => {
        if (accordion.classList.contains("open")) {
          accordion.classList.remove("open");
          accordion.style.height = `${accordion.getAttribute("data-height")}px`;
        } else {
          accordion.classList.add("open");
          accordion.style.height = `${accordion.getAttribute(
            "data-expanded-height"
          )}px`;
        }
      });
    }, 1000);
  });
});

const currencyTop = document.querySelectorAll(".currency__item_top");
const currencyBottom = document.querySelectorAll(".currency__item_bottom");
const currencyListTop = document.querySelector("#currency-list-top");
const currencyListTBottom = document.querySelector("#currency-list-bottom");

currencyTop.forEach((n) => {
  n.addEventListener("click", () => {
    currencyListTop.classList.toggle("currency__items_active");
  });
});

currencyBottom.forEach((n) => {
  n.addEventListener("click", () => {
    currencyListTBottom.classList.toggle("currency__items_active");
  });
});

const input = document.querySelector("#slider");

document.addEventListener("DOMContentLoaded", () => {
  if (localStorage.getItem("theme") === "dark") {
    input.checked = true;
    document.body.classList.add("dark");
  }
});

input.addEventListener("change", (e) => {
  if (e.target.checked) {
    document.body.classList.add("dark");
    localStorage.setItem("theme", "dark");
  } else {
    document.body.classList.remove("dark");
    localStorage.setItem("theme", "light");
  }
});
